    //////////////////// Init Select Option  ////////////////////
    private async initSelectOption(): Promise<void> {
        await this.initSelectOptionType();
        await this.initSelectOptionMatch();
        await this.initSelectOptionGroup();
    }

    private initSelectOptionType(): void {
        this.selectOptionItem.type = [
            { objectId: EType.first, name: this.$i18n.Table_Type_First },
            { objectId: EType.merge, name: this.$i18n.Table_Type_Merge },
            { objectId: EType.raw, name: this.$i18n.Table_Type_Raw },
        ];
    }

    private initSelectOptionMatch(): void {
        this.selectOptionItem.isMatch = [
            { objectId: 'match', name: this.$i18n.Table_Type_Match },
            { objectId: 'notMatch', name: this.$i18n.Table_Type_Not_Match },
        ];
    }

    private async initSelectOptionGroup(): Promise<void> {
        this.selectOptionItem.personGroup = [];

        let data: any[] = JSON.parse(JSON.stringify(PERSON_GROUPS));
        data.forEach((item) =>
            this.selectOptionItem.personGroup.push({
                objectId: item.objectId,
                name: item.name,
            }),
        );
    }

    //////////////////// Table ////////////////////
    private async tableGetApiData(tableApiParam: ITableApiParam): Promise<void> {
        this.formItem.isClearState = false;

        let param = JSON.parse(JSON.stringify(tableApiParam));

        if (this.formItem.isSearch) {
            param.paging.page = 1;
        }

        this.loading.show();

        let data = this.getFakeData(tableApiParam);
        this.tableSetData(data);

        this.formItem.isClearState = true;

        this.loading.hide();
    }

    private getFakeData(tableApiParam): any {
        let data: any[] = JSON.parse(JSON.stringify(TABLE_REPORTS));

        // keyword
        if (!!tableApiParam?.keyword) {
            const keywordFields: string[] = ['personName', 'personEmployeeId', 'personCard'];
            let keyword: string = tableApiParam.keyword.toLowerCase();
            data = data.filter((item) => {
                let isMatch: boolean = false;

                for (let field of keywordFields) {
                    let target: string = item[field];
                    if (!!target && target.toLowerCase().includes(keyword)) {
                        isMatch = true;
                        break;
                    }
                }

                return isMatch;
            });
        }

        // group
        if (Array.isArray(tableApiParam.groupIds) && tableApiParam.groupIds.length > 0) {
            data = data.filter((item) => {
                let isMatch: boolean = false;
                if (Array.isArray(item.personGroups)) {
                    let groupIds: string[] = item.personGroups.map((group) => group.objectId);
                    for (let id of groupIds) {
                        if (tableApiParam.groupIds.indexOf(id) > -1) {
                            isMatch = true;
                            break;
                        }
                    }
                }

                return isMatch;
            });
        }

        // score
        if (tableApiParam.scoreMin !== null && tableApiParam.scoreMin !== undefined) {
            data = data.filter((item) => this.getPercentageNum(item.score) >= tableApiParam.scoreMin);
        }
        if (tableApiParam.scoreMax !== null && tableApiParam.scoreMax !== undefined) {
            data = data.filter((item) => this.getPercentageNum(item.score) <= tableApiParam.scoreMax);
        }

        // isMatch
        if (tableApiParam.isMatch !== null && tableApiParam.isMatch !== undefined) {
            data = data.filter((item) => item.isMatch === tableApiParam.isMatch);
        }

        // startDate
        if (tableApiParam.startDate !== null && tableApiParam.startDate !== undefined) {
            data = data.filter((item) => {
                let itemStartDate = ServiceDatetime.dateStart(new Date(item.date));
                return itemStartDate >= new Date(tableApiParam.startDate);
            });
        }

        // endDate
        if (tableApiParam.endDate !== null && tableApiParam.endDate !== undefined) {
            data = data.filter((item) => {
                let itemEndDate = ServiceDatetime.dateEnd(new Date(item.date));
                return itemEndDate <= new Date(tableApiParam.endDate);
            });
        }

        // time
        let startTime: Date = new Date(tableApiParam.startTime);
        let endTime: Date = new Date(tableApiParam.endTime);
        if (startTime.toString() !== 'Invalid Date' || endTime.toString() !== 'Invalid Date') {
            startTime = startTime.toString() === 'Invalid Date' ? undefined : startTime;
            endTime = endTime.toString() === 'Invalid Date' ? undefined : endTime;
            let timeRange: number[][] = this.getTimeRange(startTime, endTime);

            if (timeRange.length > 0) {
                data = data.filter((item) => {
                    let isMatch: boolean = false;

                    for (let n of timeRange) {
                        if (item.timePoint >= n[0] && item.timePoint < n[1]) {
                            isMatch = true;
                            break;
                        }
                    }

                    return isMatch;
                });
            }
        }

        // sorting
        if (!!tableApiParam?.sorting?.field && (tableApiParam?.sorting?.order === 0 || tableApiParam?.sorting?.order === 1)) {
            let sortingField: string = tableApiParam?.sorting?.field;
            data = data.sort((a, b) => {
                if (tableApiParam.sorting.order === 0) {
                    if (typeof a[sortingField] === 'number') {
                        return a[sortingField] - b[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return 1;
                    if (a[sortingField] < b[sortingField]) return -1;
                } else {
                    if (typeof a[sortingField] === 'number') {
                        return b[sortingField] - a[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return -1;
                    if (a[sortingField] < b[sortingField]) return 1;
                }

                return 0;
            });
        }

        // pagination
        let pageSize: number = tableApiParam?.pageSize || this.tableApiParam.paging.pageSize;
        let total: number = data.length;
        let totalPages: number = Math.ceil(total / pageSize);
        let page: number = tableApiParam?.page || this.tableApiParam.paging.page;
        if (page > totalPages) {
            page = totalPages || 1;
        }

        this.tableItem.paging.pageSize = pageSize;
        this.tableItem.paging.page = page;
        this.tableItem.paging.totalPages = totalPages;
        this.tableItem.paging.total = total;

        data = data.slice((page - 1) * pageSize, page * pageSize);

        this.tableApiParam = JSON.parse(JSON.stringify(tableApiParam));

        return data;
    }

    private getTimeRange(start: Date, end: Date): number[][] {
        try {
            let startHour: number = !start ? 0 : start.getUTCHours();
            let startTime: number = !start ? 0.0 : parseFloat(ServiceDatetime.toUTCString(start, 'HH.mmss'));

            let endHour: number = !end ? 24 : end.getUTCHours();
            let endTime: number = !end ? 24.0 : parseFloat(ServiceDatetime.toUTCString(end, 'HH.mmss'));

            let timeRange: number[][] = [[startTime]];
            for (let i: number = 0, j: number = startHour; true; j) {
                if (j === 25) {
                    timeRange[++i] = [0];
                    j = -1;
                }

                timeRange[i][1] = j;

                if (j++ === endHour) {
                    timeRange[i][1] = endTime;
                    break;
                }
            }

            timeRange = timeRange.filter((n) => n[0] !== n[1]);

            return timeRange;
        } catch (e) {
            throw e;
        }
    }

    private async tableReload(tableParam: IComponent.ITableParam): Promise<void> {
        if (tableParam) {
            this.tableParam.page = tableParam.page ? tableParam.page : this.tableItem.paging.page;
            this.tableParam.pageSize = tableParam.pageSize ? tableParam.pageSize : this.tableItem.paging.pageSize;
            this.tableParam.sortingField = tableParam.sortingField;
            this.tableParam.sortingOrder = tableParam.sortingOrder;

            let tempTableApiParam = JSON.parse(JSON.stringify(this.tableApiParam));

            const { page, pageSize, sortingOrder, sortingField }: IComponent.ITableParam = this.tableParam;

            this.tableApiParam.paging.page = page ?? 1;
            this.tableApiParam.paging.pageSize = pageSize ?? 10;

            tempTableApiParam.sorting = {};
            if (sortingField !== undefined && sortingOrder !== undefined) {
                tempTableApiParam.sorting.field = sortingField;
                tempTableApiParam.sorting.order = sortingOrder;
            } else {
                delete tempTableApiParam.sorting;
            }

            await this.tableGetApiData(tempTableApiParam);
        }
    }

    private tableSetData(result: any[]): void {
        let tableData: ITableData[] = [];

        (result || []).forEach((element) => {
            tableData.push({
                objectId: element.objectId ? element.objectId : '',
                date: ServiceResolveText.resolveDate(element.date),
                time: ServiceResolveText.resolveDate(element.date, EDatetimeFormat.timeHHmm),
                personEmployeeId: element.personEmployeeId ? element.personEmployeeId : '',
                personName: element.personName ? element.personName : '',
                personGroup: element.personGroups ? element.personGroups : [],
                personEmail: element.personEmail ? element.personEmail : '',
                snapshotSrc: ServiceResolveText.resolvePhotoIsUrlOrBase64(element.snapshotSrc),
                isMatch: ServiceResolveText.resolveBooleanToString(element.isMatch, this, 'match', 'not match'),
                score: `${this.getPercentageNum(element.score)}%`,
            });
        });

        this.tableItem.datas = tableData;
    }

    //////////////////// Filter ////////////////////

    private fitlerConfigValues(value: any, disabledBtuuon: boolean, id: string) {
        for (const key in this.filterData) {
            if (this.filterData.hasOwnProperty(key)) {
                if (id === key) {
                    this.filterData[id] = value;
                }
            }
        }
    }

    private getPercentageNum(value: number): number {
        return (Math.round(value * 100) / 100) * 100;
    }

    private searchReset(): void {
        this.filterData = JSON.parse(JSON.stringify({ ...this.filterDataOriginal }));
        this.formItem.isClearState = false;
    }

    private async searchData(tableParam: IComponent.ITableParam): Promise<void> {
        this.tableParam.page = tableParam.page ? tableParam.page : this.tableItem.paging.page;
        this.tableParam.pageSize = tableParam.pageSize ? tableParam.pageSize : this.tableItem.paging.pageSize;
        this.tableParam.sortingField = tableParam.sortingField;
        this.tableParam.sortingOrder = tableParam.sortingOrder;

        let filterData: IFilterData = this.filterData;

        let tempTableApiParam = JSON.parse(JSON.stringify(this.tableApiParam));

        const { sortingOrder, sortingField }: IComponent.ITableParam = this.tableParam;

        this.tableApiParam.paging.page = 1;
        this.tableApiParam.paging.pageSize = 10;

        tempTableApiParam.sorting = {};
        if (sortingField !== undefined && sortingOrder !== undefined) {
            tempTableApiParam.sorting.field = sortingField;
            tempTableApiParam.sorting.order = sortingOrder;
        } else {
            delete tempTableApiParam.sorting;
        }

        if (this.filterData.keyword) {
            tempTableApiParam.keyword = this.filterData.keyword;
        } else {
            delete tempTableApiParam.keyword;
        }

        if (this.filterData.scoreMax !== null) {
            tempTableApiParam.scoreMax = this.filterData.scoreMax;
        } else {
            delete tempTableApiParam.scoreMax;
        }

        if (this.filterData.scoreMin !== null) {
            tempTableApiParam.scoreMin = this.filterData.scoreMin;
        } else {
            delete tempTableApiParam.scoreMin;
        }

        tempTableApiParam.groupIds = [];
        if (this.filterData.personGroup.length > 0) {
            tempTableApiParam.groupIds = this.filterData.personGroup.map((item) => item.objectId);
        }

